export class Rem {
    public static set = (size: number = 750) => {
        // htnml
        let html = document.documentElement;
        // 
        let width = html.clientWidth;
        // px
        let px = width / size * 100;
        // html
        html.setAttribute('style', `font-size: ${px}px !important`);
    }
    public static get = (size: number = 750) => {
        // htnml
        let html = document.documentElement;
        // 
        let width = html.clientWidth;
        // px
        let px = width / size * 100;
        // px
        return px;
    }
}


